
import { Component, OnInit, OnDestroy } from '@angular/core';
import {ContratoService} from "./contrato.service";
import {Contrato} from "./contrato.interface";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-listar-contratos',
  templateUrl: './listar-contratos.component.html',
  styleUrls: ['./listar-contratos.component.css']
})
export class ListarContratosComponent implements OnInit , OnDestroy{
  contratos: Contrato[];
  subscribe :any;
  //searchAnio : string;
  searchNumero :string;

  constructor( private route :ActivatedRoute,  private contratoService: ContratoService) { }

  ngOnInit(): void {
    let searchAnio :string;
    this.subscribe = this.route.params.subscribe(params => {
       
      //this.contratos[0].anio = params['anio']; // (+) converts string 'id' to a number

       // Here dispatch action to load the details here.
       this.contratos = this.contratoService.findAll( searchAnio, this.searchNumero);
    });
  }

  ngOnDestroy() {
    this.subscribe.unsubscribe();
  }

  ngOnClick(contrato :Contrato){
    console.log("llege aca!", contrato);
    window.location.href =  `detalle-contrato/${contrato.anio}/${contrato.numero}`;

  }
}
