import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {SharedModule} from '../shared/shared.module';
import { ListarContratosComponent } from './listar-contratos.component';
import { ContratoDetalleComponent } from './contrato-detalle.component';
import {RouterModule} from '@angular/router';
import {FormsModule} from '@angular/forms';




@NgModule({
  declarations: [ListarContratosComponent, ContratoDetalleComponent],
  exports: [
    ContratoDetalleComponent,
    ListarContratosComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    RouterModule.forChild([
      { path: '', component: ListarContratosComponent},
      { path: 'agregar-contrato', component: ContratoDetalleComponent},
      { path: 'detalle-contrato/:anio/:numero', component: ContratoDetalleComponent},
      { path: 'listar-contratos', component: ListarContratosComponent},
      { path: 'listar-contratos/:anio', component: ListarContratosComponent},
      { path: 'listar-contratos/:anio/:numero', component: ListarContratosComponent}
    ])
  ]
})
export class ContratosModule { }
