import { Component, OnInit } from '@angular/core';
import { ContratoService } from './contrato.service';
import { ActivatedRoute, UrlSegment } from '@angular/router';
import { Subscriber, Observable } from 'rxjs';
import { SubjectSubscriber } from 'rxjs/internal/Subject';
import { Contrato } from './contrato.interface';
import { $ } from 'protractor';

@Component({
  selector: 'app-contrato-detalle',
  templateUrl: './contrato-detalle.component.html',
  styleUrls: ['./contrato-detalle.component.css']
})
export class ContratoDetalleComponent implements OnInit {

  contrato: Contrato;
  agregarContrato :boolean;

  constructor( private contratoService :ContratoService,  private route :ActivatedRoute) { }

  ngOnInit(): void {

    let subs :Observable<UrlSegment[]>;
    subs = this.route.url;
    subs.subscribe( data => {
      console.log ( "route: " + data);
      if( data[0].path == "agregar-contrato")
        this.agregarContrato = true;
      else
        this.agregarContrato = false;
      this.contrato = this.contratoService.nuevoContrato();
    });
  }



  OnSubmit() :void {
    
    this.contratoService.save(this.contrato);
    console.log( `a√±o = ${this.contrato.a√±o}  anio = ${this.contrato.anio}`); 

  }

}
